.content{
    margin-top: 42px;
    @include r850{
        margin-top: 24px;
    }
    .content_inner{
        display: grid;
        grid-template-columns: 771px auto 339px;
        @include r1200{
            grid-template-columns: 55% auto 35%;
        }
        @include r650{
            grid-template-columns: auto;
        }
        grid-template-rows: auto auto;
        grid-gap: 0px 29px;
        .content_posts{
            max-width: 771px;
            .select{
                width: 100%;
                text-align-last: center;
                &_item{
                    width: 100%;      
                    text-align-last: center;   
                    padding: 16px 0;

                    option{
                        text-align-last: center; 
                        
                    }
                }
            }
            &-inner{
                display: grid;
                grid-template-columns: 374px 374px;
                @include r1200{
                    grid-template-columns: auto;
                }
                grid-column-gap: 24px;
                grid-row-gap: 24px;
                margin-top: 24px;
                &_item{
                    @include r1200{
                        grid-column-start: span 2;
                    }
                    background: #FFFFFF;
                    border-radius: 8px;
                    box-shadow: 0px 12px 24px rgba(171, 199, 176, 0.45);
                    .post_img{
                        width: 100%;
                        height: 187px;
                    }
                    .post_txt-inner{
                        padding: 16px;
                    }
                    .post_caption{
                        font-style: normal;
                        font-weight: 600;
                        font-size: 20px;
                        line-height: 27px;
                        color: #293241;
                        
                    }
                    .post_desc{
                        font-style: normal;
                        font-weight: normal;
                        font-size: 16px;
                        line-height: 24px;
                        color: #293241;
                        margin-top: 12px;
                        margin-bottom: 16px;
                    }
                    .post_info-inner{
                        display: flex;
                        p{
                            font-weight: 500;
                            font-size: 14px;
                            line-height: 20px;
                            color: #8795AA;
                        }

                        .post_comments-inner{                            
                            margin-left: auto;
                            .post_comments{
                                display: flex;
                                align-items: center;
                            }
                            svg{
                                margin-right: 7px;
                            }
                        }
                        .post_viewers-inner{
                            margin-left: 17px;
                            .post_viewers{
                                display: flex;
                                align-items: center;
                            }
                            svg{
                                margin-right: 5px;
                            }
                        }
                    }
                }
            }
        }

        .line{
            width: 1px;
            margin: 0 auto;
            background-color: rgba(41, 50, 65, 0.12);
        }

        .content_aside{
            @include r850{
                margin-top: 56px;
            }
            .useful_caption{
                display: block;
                font-weight: 600;
                font-size: 20px;
                line-height: 27px;
                color: #293241;
                margin-bottom: 8px;
            }
            .useful_inner{
                .useful_link{
                    display: block;
                    font-weight: normal;
                    font-size: 16px;
                    line-height: 24px;
                    color: #637288;
                    padding: 16px 0;
                    border-top: 1px solid rgba(41, 50, 65, 0.12);
                    &:first-child{
                        border-top: 1px solid transparent;

                    }
                }
            }

            .popular_inner{
                margin-top: 16px;
                @include r650{
                    display: none;
                }
                .popular_posts{
                    margin-bottom: 32px;
                }
                .popular_caption{
                    display: block;
                    font-weight: 600;
                    font-size: 20px;
                    line-height: 27px;
                    color: #293241;
                    margin-bottom: 24px;
                }
                .popular_posts-img{
                    width: 100%;
                    height: 170px;
                    border-radius: 8px;
                    margin-bottom: 12px;
                }
                .popular_posts-txt{
                    font-style: normal;
                    font-weight: 600;
                    font-size: 16px;
                    line-height: 22px;
                    color: #293241;
                }
            }
        }
    }
}