.header{
    background: #FFFFFF;
    box-shadow: 0px 1px 0px rgba(0, 0, 0, 0.12);
    padding-top: 8px;
    padding-bottom: 8px;
    .header_top{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    }
    .nav_list{
        padding-left: 0;
        @include r800{
            display: none;
        }
    }
    .nav_list-item {
        display: inline-block;
        transition: 0.3s ease;
        margin-left: 32px;
        a{
            font-weight: 500;
            font-size: 14px;
            line-height: 20px;
            color: #293241;
        }
    }
    .social{
        margin-left: auto;
        padding-top: 6px;
        .social_link:first-child{
            margin-right: 20px;
        }
    }
    .searching{
        position: relative;
        margin-left: 32px;
        @include r1040{
            display: none;
        }
        label{
            position: absolute;
            left: 15px;
            top: 11px;
        }
        .search_inp{
            max-width: 237px;
            background: #EFEFEF;
            border-radius: 8px;
            border:1px solid #EFEFEF;
            padding: 10px 48px;
            padding-right: 0;
            height: 40px;
            outline: none;
            color: #9E9EA7;
            &:active{
                background-color: #fff;
                color: #000000;
            }
            &:focus{
                background-color: #fff;
                color: #000000;
                border: 1px solid #E1E1E1;
            }
        }
    }
    .color_mode{
        width: 40px;
        height: 40px;
        background: rgba(73, 85, 104, 0.08);
        border-radius: 8px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-left: 20px;
        cursor: pointer;
    }
}
