.footer{
    background: #0B0D17;
    margin-top: 112px;
    @include r850{
        margin-top: 40px;
    }
    &_info{
        display: flex;
        padding: 32px 0;
        border-bottom: 1px solid #ffffff23;
        @include r560{
            flex-direction: column;
         }
    }
    &_img{
        width: 56px;
        height: 56px;
        border-radius: 8px;
        background-image: url(../img/footerimg.webp);
        @include r560{
            width: 100%;
            height: 90px;
            background-size: cover;
            background-repeat: no-repeat;
            background-image: url(../img/ftimagemob.jpg);
         }
    }
    &_txt{
        max-width: 809px;
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        color: #FFFFFF;
        margin-left: 16px;
        @include r930{
           width: 100%;
        }
        @include r560{
            margin-left: 0;
            margin-top: 16px;
         }
    }
    &_b{
        padding: 26px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include r600{
            flex-direction: column;
            align-items: flex-start;
        }  
        &-links{
            .footer_ul{
                &-list{
                    display: inline-block;
                    font-style: normal;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 20px;
                    color: #FFFFFF;
                    &:not(:first-child){
                        margin-left: 32px;
                        @include r350{
                            display: block;
                            margin-left: 0;
                            margin-bottom: 5px;
                        }
                    }                    
                    &:first-child{
                        @include r600{
                            display: block;
                            margin-bottom: 24px;
                        }
                    }
                    &:nth-child(2){
                        @include r600{
                            margin-left: 0;
                        }                        
                    }
                }
            }
        }
        &-social{
            margin-top: 0;
            @include r600{
                margin-top: 24px;
            }
        }
        .tg_footer{
            margin-left: 20px;
            margin-top: 4px;
        }
        .vk_footer{
            svg{
                margin-top: 4px;
            }
        }
    }
}