body{
    background-color: #fff;
    font-family: 'Noto Sans', sans-serif;
    color: #293241;
    margin: 0;
}
ul{
    list-style: none;
    padding: 0;
}
a{
    text-decoration: none;
    color: inherit;
}
.container{
    max-width: 1198px;
    margin: 0 auto;
    padding: 0 15px;
}

@import '_header.scss';

.main_info-section{
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.09), rgba(0, 0, 0, 0.09)), linear-gradient(250.82deg, #86EF9C 0%, #68BF7A 100%);
    padding: 65px 0;   
    @include r850{
        padding: 0;
    }
    .main_info{
        position: relative;        
        @include r850{
            overflow:hidden;
        }
    }
    .main_txt{
        max-width: 836px;
        @include r850{
            margin: 40px 16px;
        }
        h1{
            font-style: normal;
            font-weight: 600;
            font-size: 32px;
            line-height: 44px;
            color: #FFFFFF;
            margin-bottom: 12px;
            @include r850{
                font-size: 24px;
                line-height: 33px;
            }
        }
        h2{
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 24px;
            color: #FFFFFF;
            @include r850{
                font-weight: 500;
                font-size: 14px;
                line-height: 22px;
            }
        }
    } 
    .main_img{
        position: absolute;
        right: -10%;
        top: -65px;
        height: 302px;
        @include r1332{
            right: 0;
        }
        @include r850{
            right: 0;
            top: 0;
            height: 100%;
        }

        img{
            height: 100%;
        }
    }
}

.subhead-section{
    padding: 32px 0;
    background-color: #F9F9F9;
    .subhead_list{
        display: flex;
        justify-content: space-between;
        @include r1100{
            overflow-x: overlay;
        }
        &-item{
            background: rgba(41, 50, 65, 0.08);
            border-radius: 8px;
            min-width: 146px;
            @include r1100{
               margin-right: 24px;
            }
            @include r850{
                min-width: 121px;
             }
            a{
                display: block;
                width: 100%;
                height: 100%;
                font-weight: 500;
                font-size: 16px;
                line-height: 24px;
                text-align: center;
                color: #293241;
                padding: 8px 0;
            }
            &.active{
                background: #71CA82;
                box-shadow: 0px 12px 24px rgba(113, 202, 130, 0.45);
                a{
                    color: #FFFFFF;
                }
            }
        }
    }
}









